

















import './users.scss';
import { IComponent } from '@/core/interfaces/component.interface';
import { mapActions, mapGetters } from 'vuex';
import { localeFilter } from '@/core/filters/locale.filter';

export default {
  computed: {
    ...mapGetters(['users']),
  },
  async mounted() {
    const usersPage = this as unknown as IComponent;
    await usersPage.$store.dispatch('loadUsers');
  },
  methods: {
    ...mapActions(['loadRecords']),
    deleteUser(id: number) {
      if (confirm(localeFilter('confirm_user_delete'))) {
        const adminPage = this as unknown as IComponent;
        adminPage.$store.dispatch('deleteUser', { id });
      }
    },
  },
};
